.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    height: 25px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    background-color: transparent;
}

.nav__black {
    background-color: #111;
    z-index: 1000;
    transition: background-color 1s;
}

.logo {
    position: fixed;
    left: 20px;
    width: 80px;
}

.avatar {
    position: fixed;
    right: 20px;
    width: 30px;
}